@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&amp;family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&amp;display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
.route_app{
    min-height: 100vh;
}
button:active{
    opacity: 0.2 !important;
}
 .title-ext{
    font-size: 30px !important;
 }

 .work-content{
     padding:0px 20px;
     
 }
 .banner-img{
     display: flex;
     justify-content: center;
 }
 /* .banner-img > img{
     max-width: 90% !important;
 } */
 
 .comment_a_marche{
    background: #f2f2f2;
    border-radius: 50px;
    padding: 0 15px;
 }
 .fa-map-marker-alt{
    -webkit-transform: translate(3px,-1px);
            transform: translate(3px,-1px);
    margin-right: 8px;
    font-size: 35px !important;
 }
.transition_opacity{
    opacity: 0;
}
 

.assurence_box{
    height: 350px;
position: relative;
box-shadow: 0 0 66px #00000052;
}

.assurence_box > .btn-primary{
    position: absolute;
    bottom: 20px;
    left: 30px;
}

.feature-bx4 .icon-content .ttr-title {
    color: #f17732 !important;
    margin: 0 0 12px;
}

.LOGO {
    height: 27px !important;
    margin-top: 4px !important;
}

.banner-img > img {
    width: 400px !important;
    height:auto !important;
}

.work-num-bx > img {
 max-width:300px;
 max-height: 300px;
}
.main-banner{
    height:450px !important
}

.logo_partenaire{
    width: 250px;
    margin: 10px;
    cursor: pointer;
    border-radius: 60px;
    transition:.3s;
}
.logo_partenaire:hover{
    -webkit-filter: opacity(0.8);
            filter: opacity(0.8);
}

.heading-bx > h6 > b{
    color: #f17732 !important;
    font-weight: 700 !important;
    text-decoration: underline;
    cursor: pointer;
}
.footer_icon{
    margin-right: 15px;
    width: 40px !important;
    height: auto !important;
}

.w100{
    width:100%
}
@media only screen and (max-width: 575px) {
    .main-banner{
        height:auto !important
    }
.img_banner{
    margin-top: 20px;
    margin-bottom: 30px;
}
    .main-banner {
        height: auto;
        padding-top: 50px !important;
    }
}

::-webkit-scrollbar {
    width: 5px;    height: 8px;

  }
  
  ::-webkit-scrollbar-thumb:horizontal{
    background: rgb(116, 116, 116); 
}
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: rgb(116, 116, 116); 
   
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #242424; 
  }


  input{
      border-radius: 0 !important;
  }


  button{
      border-radius: 0px !important;
  }
.App {
  text-align: center;
}
.container_app{
  min-height: 100vh;
  margin-top: 80px; 
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App {
  background-color: #282c34;
  min-height: 100vh;
 
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

